import React from "react"
import { connect } from 'react-redux'
import styled from "styled-components"
import { unit, color16 } from '../../utils/utils'


export const Tag = styled.div`
  display: flex;
  background: ${props => props.color};
  justify-content: center;
  align-items: center;
  height: 1.5rem;
  padding: 0 .5rem;
  font-size: 14px;
  margin-left: 1rem;
  margin-top: .5rem;
`

const TagsContainer = styled.div`
  display: flex;
  flex-flow: row wrap;
`



const Tags = ({ allTags }) => {
  const clickEvent = () => { };
  return (
    <TagsContainer>
      {allTags.map(item => (
        <Tag onClick={() => clickEvent && clickEvent(item)} color={color16()}>{item}</Tag>
      ))}
    </TagsContainer>
  )
}

const mapDispatchToProps = null;

const mapStateToProps = (state, ownProps) => {
  return {
    ...ownProps,
    allTags: unit(state._allArticles.articles.map(({ node }) => (node.frontmatter.tag))),
  }
}

const Connected = connect(
  mapStateToProps,
  mapDispatchToProps
)(Tags)

export default Connected;
