// 
// unit([1, 2, 3, 1])
export const unit = arr =>
  arr.reduce((result, current) => {
    if (!result.includes(current)) {
      result.push(current)
    }

    return result
  }, [])

const colorsArr = [
  '#eecf55',]
export function color16(){//
  const random = Math.floor(Math.random() * 10);
  return colorsArr[random] ? colorsArr[random] : color16;
};
